@import url(https://rsms.me/inter/inter.css);
html {
  font-family: 'Inter', sans-serif;
}

@supports (font-variation-settings: normal) {

  html,
  pre {
    font-family: 'Inter var', sans-serif;
  }
}

html, body {margin: 0; height: 100%; overflow: hidden}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#map {
    width: 100vw;
    height: 100vh;
}
button:focus {
    outline: none;
}

.modal {
    position: absolute;
    z-index: 100;
    bottom: 0;
    height: 66vh;
    background-color: white;
    border-top-right-radius: 8%;
    border-top-left-radius: 8%;
    width: 100%;
    transition: transform 0.5s;
}

h1 {
    flex-grow: 1;
}

.modalclose {
    width: 30px;
    height: 30px;
    margin-right: 0px;
}

.modal2 { /*peek*/
    transform: translate(0px, 56vh);
}

.modal2intro {
    -webkit-animation: modal2animation ease 0.1s;
            animation: modal2animation ease 0.1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

.modal1 { /*closed*/
    transform: translate(0px,66vh);
}

.modal0 { /*open*/
    transform: translate(0px,0px);
}

.modal0intro {
    -webkit-animation: modal0animation ease 0.5s;
            animation: modal0animation ease 0.5s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

.modaltop {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 10vh;
    margin-right: 0;
}

.modal-link-container {
    margin: 20px;
    width: 80%;
    text-align: center;
    text-decoration: none;
    background: grey;
    padding: 20px;
    border-radius: 10px;
    color: white;
    font-weight: 200;
}

.modal-link {
    text-decoration: none;
}

.modal > * {
    margin-left: 20px;
    margin-right: 20px;
}

@-webkit-keyframes modal0animation{
    0% {
        transform:  translate(0px,66vh)  ;
    }
    100% {
        transform:  translate(0px,0px)  ;
    }
}

@keyframes modal0animation{
    0% {
        transform:  translate(0px,66vh)  ;
    }
    100% {
        transform:  translate(0px,0px)  ;
    }
}

@-webkit-keyframes modal2animation{
    0% {
        transform:  translate(0px,66vh)  ;
    }
    100% {
        transform:  translate(0px,56vh)  ;
    }
}

@keyframes modal2animation{
    0% {
        transform:  translate(0px,66vh)  ;
    }
    100% {
        transform:  translate(0px,56vh)  ;
    }
}
li:first-child {
    border-top: 1px solid lightgrey;
}

li {
    list-style: none;
    font-size: 20px;
    padding-bottom: 6px;
    padding-top: 6px;
    border-bottom: 1px solid lightgrey;
    margin-right: 20px;
}

ul {
    padding: 0;
}
